a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  padding: 0;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

* {
  transition: all .2s cubic-bezier(.21, .24, .58, .98);
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #e8e8e2;
  font-family: Neue Montreal;
  font-weight: 500;
}

.is-hidden {
  opacity: 0;
  visibility: 0;
  pointer-events: none;
}

::selection {
  color: #fafafa;
  background-color: #6d8434;
}

.backdrop {
  background-color: #01010166;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.no-scroll {
  overflow: hidden;
}

[data-product] {
  cursor: pointer;
}

.display-none {
  display: none;
}

.main {
  padding-bottom: 80px;
}

@media screen and (width >= 768px) {
  .main {
    padding-bottom: 100px;
  }
}

@media screen and (width >= 1304px) {
  .main {
    padding-bottom: 61px;
  }
}

.main__body {
  flex-direction: column;
  gap: 80px;
  display: flex;
}

@media screen and (width >= 768px) {
  .main__body {
    gap: 100px;
  }
}

@media screen and (width >= 1304px) {
  .main__body {
    flex-direction: row;
    justify-content: center;
    gap: 40px;
  }
}

.main__left {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

@media screen and (width >= 768px) {
  .main__left {
    gap: 50px;
  }
}

@media screen and (width >= 1304px) {
  .main__left {
    width: 925px;
  }
}

.main__right {
  flex-direction: column;
  gap: 80px;
  display: flex;
}

@media screen and (width >= 768px) {
  .main__right {
    gap: 64px;
  }
}

@media screen and (width >= 1304px) {
  .main__right {
    gap: 40px;
    width: 275px;
  }
}

.container {
  max-width: 365px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (width >= 768px) {
  .container {
    width: 734px;
    max-width: none;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1270px;
    max-width: none;
  }
}

.header {
  height: 96px;
  padding: 24px 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  fill: #010101;
  border-radius: 9px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 165px;
  height: 38px;
  transition: background, fill, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (width >= 768px) {
  .header__logo {
    width: 220px;
    height: 60px;
  }
}

.header__logo:hover {
  fill: #e8e8e2;
  background: #6d8434;
}

.header__logo:hover .header__logo--circle {
  background-color: #e8e8e2;
}

.header__logo:hover .header__logo--icon {
  fill: #6d8434;
}

.header__logo:focus {
  fill: #e8e8e2;
  background: #6d8434;
  outline: none;
}

.header__logo:focus .header__logo--circle {
  background-color: #e8e8e2;
}

.header__logo:focus .header__logo--icon {
  fill: #6d8434;
}

.header__logo--circle {
  background-color: #6d8434;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (width >= 768px) {
  .header__logo--circle {
    width: 32px;
    height: 32px;
  }
}

.header__logo--icon {
  fill: #e8e8e2;
  width: 20px;
  height: 20px;
  transition: fill .3s cubic-bezier(.4, 0, .2, 1);
}

.header__logo--text {
  width: 118px;
  height: 20px;
}

@media screen and (width >= 768px) {
  .header__logo--text {
    width: 157px;
    height: 24px;
  }
}

@media screen and (width >= 1200px) {
  .header__logo--text {
    width: 155.026px;
    height: 20.616px;
  }
}

.header__nav {
  justify-content: center;
  align-items: center;
  gap: 6px;
  display: flex;
}

@media screen and (width >= 768px) {
  .header__nav {
    gap: 16px;
  }
}

@media screen and (width >= 1200px) {
  .header__nav {
    gap: 24px;
  }
}

.header__link {
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  width: 77px;
  height: 38px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (width >= 768px) {
  .header__link {
    width: 104px;
    height: 48px;
  }
}

.header__link:hover {
  background-color: #586f1f;
}

.header__link:focus {
  background-color: #586f1f;
  outline: none;
}

.header__cart {
  fill: #e8e8e2;
  color: #010101;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 38px;
  padding: 0;
  transition: background, fill, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (width >= 768px) {
  .header__cart {
    height: 48px;
    padding: 0 8px;
  }
}

.header__cart:hover {
  fill: #586f1f;
  color: #e8e8e2;
  background: #586f1f;
}

.header__cart:hover .header__cart--circle {
  background-color: #e8e8e2;
}

.header__cart:focus {
  fill: #586f1f;
  color: #e8e8e2;
  background: #586f1f;
  outline: none;
}

.header__cart:focus .header__cart--circle {
  background-color: #e8e8e2;
}

.header__cart--circle {
  background-color: #6d8434;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  width: 38px;
  height: 38px;
  transition: background .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__cart--icon {
  width: 18px;
  height: 18px;
}

.header__cart--link {
  text-transform: uppercase;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 133.333%;
  transition: color .3s cubic-bezier(.4, 0, .2, 1);
  display: none;
}

@media screen and (width >= 768px) {
  .header__cart--link {
    display: block;
  }
}

.header__home {
  color: #e8e8e2;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 128.571%;
}

@media screen and (width >= 768px) {
  .header__home {
    font-size: 18px;
    font-weight: 500;
    line-height: 133.333%;
  }
}

.basket {
  padding-top: 57px;
  padding-bottom: 100px;
}

.basket__cart {
  align-items: center;
  justify-content: none;
  gap: 14px;
  display: flex;
}

.basket__cart--circle {
  fill: #e8e8e2;
  background-color: #6d8434;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 42px;
  height: 42px;
  display: flex;
}

.basket__cart--icon {
  width: 18px;
  height: 18px;
}

.basket__title {
  color: #010101;
  text-transform: uppercase;
  font-size: 32px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
}

.basket__btn {
  color: #010101;
  cursor: pointer;
  fill: #010101;
  background-color: #e8e8e2;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin-left: auto;
  margin-right: 23px;
  padding: 5px 12px;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 125%;
  transition: background-color, color, all .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.basket__btn:hover {
  color: #e8e8e2;
  fill: #e8e8e2;
  background-color: #6d8434;
}

.basket__btn:focus {
  color: #e8e8e2;
  fill: #e8e8e2;
  background-color: #6d8434;
  outline: none;
}

.basket__icon {
  width: 24px;
  height: 24px;
}

.basket__wraper {
  align-items: none;
  justify-content: space-between;
  display: flex;
}

.basket__products {
  padding-top: 16px;
}

.basket__list {
  width: 549px;
  height: 520px;
  margin-top: 46px;
  padding-right: 30px;
  overflow-x: hidden;
}

.basket__list::-webkit-scrollbar {
  width: 9px;
}

.basket__list::-webkit-scrollbar-thumb {
  background: #01010114;
  border-radius: 20px;
}

.basket__product {
  align-items: center;
  justify-content: none;
  border-bottom: 1px solid #0101011a;
  gap: 40px;
  padding: 40px 0;
  display: flex;
  position: relative;
}

.basket__product--square {
  background: #fafafa;
  border-radius: 10px;
  width: 120px;
  height: 120px;
}

.basket__product--content {
  align-items: none;
  justify-content: none;
  flex-direction: column;
  gap: 12px;
  height: 120px;
  display: flex;
}

.basket__product--title {
  color: #010101;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}

.basket__product--wrap {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.basket__product--text {
  color: #0101014d;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 116.667%;
}

.basket__product--bold {
  color: #010101;
  margin-left: 4px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 128.571%;
}

.basket__product--price {
  color: #010101;
  margin-top: 32px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}

.basket__product--number {
  color: #010101;
  top: 4px;
  left: calc(50% - width / 2);
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%;
  position: absolute;
}

.basket__product--close {
  cursor: pointer;
  fill: #010101;
  background-color: #e8e8e2;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 40px;
  right: 30px;
}

.basket__product--close:hover {
  fill: #e8e8e2;
  background-color: #6d8434;
}

.basket__product--close:focus {
  fill: #e8e8e2;
  background-color: #6d8434;
  outline: none;
}

.basket__product--icon {
  width: 20px;
  height: 20px;
  fill: inherit;
  transition: fill .3s cubic-bezier(.4, 0, .2, 1);
}

.basket__product--controls {
  border: 1px solid #0101011a;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  gap: 46px;
  width: 115px;
  height: 34px;
  display: flex;
  position: absolute;
  bottom: 40px;
  right: 0;
}

.basket__product--btn {
  cursor: pointer;
  background-color: #e8e8e2;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.basket__product--btn:hover {
  background-color: #6d8434;
}

.basket__product--btn:focus {
  background-color: #6d8434;
  outline: none;
}

.basket__product--minus {
  background-color: #010101;
  border-radius: 2px;
  width: 12px;
  height: 2px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1);
}

.basket__product--btn:hover .basket__product--minus, .basket__product--btn:focus .basket__product--minus {
  background-color: #e8e8e2;
}

.basket__product--plus {
  background-color: #010101;
  border-radius: 2px;
  width: 12px;
  height: 2px;
  position: absolute;
  top: 8px;
  left: 3px;
}

.basket__product--plus:last-child {
  width: 2px;
  height: 12px;
  top: 2.5px;
  left: 8px;
}

.basket__product--btn:hover .basket__product--plus, .basket__product--btn:focus .basket__product--plus {
  background-color: #e8e8e2;
}

.basket__product:first-child {
  padding-top: 0;
}

.basket__product:first-child .basket__product--close {
  top: 0;
}

.basket__product:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.basket__product:last-child .basket__product--controls {
  bottom: 0;
}

.basket__order {
  align-items: none;
  flex-direction: column;
