  width: 590px;
  height: 60px;
  padding: 14px 28px;
  display: flex;
}

.basket__text {
  margin-top: 8px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}

.basket__sum {
  justify-content: center;
  align-items: end;
  gap: 8px;
  display: flex;
}

.basket__sum--text {
  color: #01010133;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 116.667%;
}

.basket__sum--number {
  color: #010101;
  font-size: 32px;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
}

.basket__form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
  position: relative;
}

.basket__input {
  background-color: #e8e8e2;
  border: 1px solid #0101014d;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 590px;
  padding: 15px 0 15px 91px;
  transition: outline .3s cubic-bezier(.4, 0, .2, 1);
