}

.basket__input--text {
  color: #01010180;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 133.333%;
  position: absolute;
  top: 14px;
  left: 20px;
}

.basket__input--text:nth-child(2) {
  top: 84px;
}

.basket__submit {
  color: #e8e8e2;
  cursor: pointer;
  background-color: #6d8434;
  border-radius: 30px;
  gap: 10px;
  width: 590px;
  padding: 15px 65px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
