}

.basket__submit:hover {
  color: #6d8434;
  background-color: #e8e8e2;
  box-shadow: 0 0 25px 10px #6d843480;
}

.basket__submit:focus {
  color: #6d8434;
  background-color: #e8e8e2;
  outline: none;
  box-shadow: 0 0 25px 10px #6d843480;
}

.order {
